import React from 'react'
import { Link } from 'react-router-dom'
import '../App.css'
function Home() {
    return (
        <div className="home__container">
        <Link className="link" to="/rock-paper-scissors"><span>Rock / Paper / Scissors</span></Link>
        <Link className="link" to="/double-memory"><span>Double Memory</span></Link>
        </div>
    )
}

export default Home
