import React, {useState, useEffect} from 'react'
import './Rockps.css'

import Paper from '../images/paper.svg'
import Rock from '../images/rock.svg'
import Scissors from '../images/scissors.svg'

function Rockps() {
    // const [top,setTop] = useState()
    // const [bottom,setBottom] = useState()
    // const [rotate,setRotate] = useState()
    const [cardset, setCardset] = useState()
    const [instruct, setInstruct] = useState(false)
    const [menu, setMenu] = useState(true)
    const [pick, setPick] = useState(3)
    const [playercards, setPlayercards] = useState([])
    const [compcards, setCompcards] = useState([])
    const [compfinish, setCompfinish] = useState(false)
    const [comphasit, setComphasit] = useState([])
    const [playerscore, setPlayerscore] = useState(0)
    const [compscore, setCompscore] = useState(0)
    const [result, setResult] = useState("")
    const [cardslength,setCardslength] = useState(3)

    useEffect(()=>{
        if(cardslength === 1){
            if(playerscore === 2){
                setResult("WIN")
            }
            else if(compscore === 2){
                setResult("LOSE")
            }
        }
        else if(cardslength === 0){
            if(playerscore === compscore){
                setResult("DRAW")
            }
            if(playerscore < compscore){
                setResult("LOSE")
            }
            if(playerscore > compscore){
                setResult("WIN")
            }
        }

    },[cardslength, playerscore, compscore])


    function generateUniqueNumber(limit) {
        const randomselector = Math.floor(Math.random() * limit)
        if (limit === 3) {
            if (comphasit.includes(randomselector)) return generateUniqueNumber(3)
            else setComphasit([...comphasit, randomselector]); return randomselector
        }
    }

    function generateUniqueNumber30(arr, nc) {
        let arr2 = [...playercards, cardset[nc]]
        var rnum = Math.floor(Math.random() * 30)
        rnum = Number(rnum)
        const locate = cardset[rnum]
        if (!arr.includes(locate) && !arr2.includes(locate)) return rnum
        else return generateUniqueNumber30(arr)
    }
    let newarray = []

    const cardsHandlre = (e) => {
        let namecard = e.target.classList[0].split("card").join("")
        const player = document.querySelectorAll(".player")
        if (pick > 0) {
            setPick(pick => pick - 1)
            setPlayercards([...playercards, cardset[namecard]])
            e.target.style.opacity = 0
            e.target.style.pointerEvents = 'none'
        }
        if (pick === 3) player[0].style.transform = "translateY(0px) rotateZ(-25deg)"
        if (pick === 2) player[1].style.transform = "translateY(-20px)"
        if (pick === 1)
        {
            player[2].style.transform = "translateY(0px) rotateZ(25deg)"
            document.querySelectorAll('.comp').forEach((e, i) => {
                setTimeout(() => {
                    if(document.querySelector("#start__container")){
                        const randomselector = generateUniqueNumber30(newarray,namecard)
                        newarray.push(cardset[randomselector])
                        setCompcards(newarray)
                        e.classList.add(randomselector)
                        document.querySelector(`.card${cardset[randomselector].replace(/\D/g, '')}`).style.opacity = 0;
                        e.style.transform = i === 0 ? "translateY(0px) rotateZ(-25deg)" : i === 1 ? "translateY(-20px)" : "translateY(0px) rotateZ(25deg)"
                        if (i === 2) setTimeout(setCompfinish(true),1000)
                    }
                }, 500 * (i + 1))
            })
        }
    }

    const cardSelectedfromDeck = (e) => {
        // const targetimg = e.target.style.backgroundImage
        if (compfinish && userturn) {
            // Animate Card centering
            e.target.style.transform = `translate(0px,-${(window.innerHeight / 2) - 62}px) rotateZ(${Math.floor(Math.random() * 720) - 360}deg)`
            e.target.style.pointerEvents= `none`
            const randomselector = generateUniqueNumber(3)
            const compselect = document.querySelectorAll('.comp')[randomselector]

            // setCompcards(compcards.filter(item=> item!==compcards[randomselector]))
            const randomturner = Math.floor(Math.random() * 720) - 360
            document.querySelectorAll('.comp')[randomselector].style.transform = `translate(0px,-${(window.innerWidth / 2) - 62}px) rotateZ(${randomturner}deg)`
            document.querySelectorAll('.comp')[randomselector].classList.add('compselect')
            setUserturn(false)

            // Animate Computer Card Reveal
            setTimeout(() => {
                compselect.classList.remove("noshow")
                const getImage = cardset[compselect.classList[1]].replace(/\d/g, '')

                compselect.style.backgroundImage = `url(${getImage === 'Scissors' ? Scissors : getImage === 'Rock' ? Rock : Paper})`
                
            },1150)

            // Animate Cards removal
            setTimeout(() => {
                setUserturn(true)
                compselect.style.transform = `translate(0px,-${(window.innerWidth / 2) - 62}px) rotateZ(${randomturner}deg) rotateY(180deg)`

                if(document.querySelector('#start__container')){
                    setCardslength(cardslength => cardslength-1)
                    document.querySelector('.compselect').classList.remove('compselect')
                    const playerchoice = e.target.style.backgroundImage.search('rock')>0 ? 'rock' : e.target.style.backgroundImage.search('paper')>0 ? 'paper' : 'scissors'
                    // const compchoice = compselect.style.backgroundImage.search('rock')>0 ? 'rock' : compselect.style.backgroundImage.search('paper')>0 ? 'paper' : compselect.style.backgroundImage.search('scissors')>0 ? 'scissors' : ''
                    const compchoice = cardset[compselect.classList[2]].replace(/\d/g,'') === 'Scissors' ? 'scissors' :cardset[compselect.classList[2]].replace(/\d/g,'') === 'Paper' ? 'paper' : 'rock'
                
                    if(playerchoice === 'scissors'){
                        setPlayerscore(playerscore => compchoice === 'paper' ? playerscore + 1 : playerscore + 0)
                        setCompscore(compscore => compchoice === 'rock' ? compscore + 1 : compscore + 0)
                    }
                    if(playerchoice === 'paper'){
                        setPlayerscore(playerscore => compchoice === 'rock' ? playerscore + 1 : playerscore + 0)
                        setCompscore(compscore => compchoice === 'scissors' ? compscore + 1 : compscore + 0)
                    }
                    if(playerchoice === 'rock'){
                        setPlayerscore(playerscore => compchoice === 'scissors' ? playerscore + 1 : playerscore + 0)
                        setCompscore(compscore => compchoice === 'paper' ? compscore + 1 : compscore + 0)
                    }
                }
            }, 1000)
            setTimeout(() => { e.target.style.opacity = 0; compselect.style.opacity = 0 },1500)}
    }


    


    // const repeater = (decide) => {
        
    //     let newarray = []
    //     for (var i = 0; i < 5; i++) {
    //         newarray.push(decide === 0 ? Math.floor(Math.random() * (window.innerHeight - 50)): decide === 1 ? Math.floor(Math.random() * (window.innerWidth - 25) + 50) : Math.floor(Math.random()*360))
    //     }
    //     if (decide === 0)
    //         setTop(newarray)
    //     else if (decide === 1)
    //         setBottom(newarray)
    //     else
    //         setRotate(newarray)
    //     console.log(top)
    // }

    const menuHandlre = () => {
        setMenu(false)
        let cardsetArray = [];

        for (var i = 0; i < 30; i++) {
            const randomselector = Math.floor(Math.random() * 3)
            cardsetArray.push(randomselector===0 ? `Rock${i}`:randomselector===1 ? `Paper${i}` : `Scissors${i}`)
        }
        setCardset(cardsetArray)
    }

    const [userturn, setUserturn] = useState(true)

    const resetHandlre = (e) => {
        e.preventDefault()
        setCardset()
        setInstruct(false)
        setMenu(true)
        setPick(3)
        setPlayercards([])
        setCompcards([])
        setCompfinish(false)
        setComphasit([])
        setUserturn(true)
        setCompscore(0)
        setPlayerscore(0)
        setResult("")
        setCardslength(3)
    }

    (() => {
            // let newarray = []

            // for (var i = 0; i < 30; i++) {
            //     const randomselector = Math.floor(Math.random() * 3)
                
            //     newarray.push(randomselector===0 ? 'rock':randomselector===1 ? 'paper' : 'scissors')
            // }
            // console.log(newarray)


    })()
    return (
        <div className="rockps__container">
            {menu ?
                <div>
                    <h1>ROCK, PAPER, SCISSORS</h1>
                    <button onMouseUp={menuHandlre}>play</button>
                    <div className="instruct__button" onMouseUp={()=>setInstruct(!instruct)}>?</div>
                    {instruct ?
                        <div className="instruction__container" onMouseUp={()=>setInstruct(!instruct)}>
                            
                            <div className="instructions">
                                <p>
                                    <span style={{ fontSize: 30, marginBottom: 10 }}>Directive:</span>
                                30 cards, each draw upon them a rock, paper or scissors randomly, will be placed upon the board.<br/>
                                User will be first to pick 3 cards before CPU will have its turn.<br/><br/>
                                After both players have decided, each player will have to pick one of the three on their deck the outcome of the match.<br/>
                                Rock {`>`} Scissors {`>`} Papers {`>`} Rock. <br/>It's best of 3.<br/>The rules are simple.
                                </p>
                            </div>
                    
                        </div> : ""}
                </div> :
                <div id="start__container">
                    <button className="reset" onMouseUp={resetHandlre}>RESET</button>
                    <div className={`scoreboard ${result? 'scoreveil' : ''}`}>
                        <span className="playerscore">{playerscore}</span>
                        <span>-</span>
                        <span className="compscore">{compscore}</span>
                    </div>
                    <span className={`result ${result ? 'animate' : ''}`}>{result}</span>
                    {
                        [...new Array(30)].map((e,i) => {
                            return ( <span className="cards" onMouseUp={cardsHandlre} key={i} style={{'--r':`${(i*8)-295}deg`,animationName:compfinish ? 'notpan':'pan'}}><p className={`card${i}`}></p></span> )
                        })
                    }
                    
                     {/* (result === "WIN" || result === "LOSE" || result === "DRAW") ? "fill": "none" */}
                    <div className="playercard__holder" style={{pointerEvents: ""}}>
                        <span className="player" style={{pointerEvents: compfinish ? "fill": result !== "" ? "none" : "none", backgroundImage: playercards[0] ? `url(${playercards[0].replace(/[0-9]/g,'')==='Scissors' ? Scissors : playercards[0].replace(/[0-9]/g,'')==='Paper' ? Paper : Rock})`: ""}} onMouseUp={cardSelectedfromDeck}></span>
                        <span className="player" style={{pointerEvents: compfinish ? "fill": result !== "" ? "none" : "none", backgroundImage: playercards[1] ? `url(${playercards[1].replace(/[0-9]/g,'')==='Scissors' ? Scissors : playercards[1].replace(/[0-9]/g,'')==='Paper' ? Paper : Rock})`: ""}} onMouseUp={cardSelectedfromDeck}></span>
                        <span className="player" style={{pointerEvents: compfinish ? "fill": result !== "" ? "none" : "none", backgroundImage: playercards[2] ? `url(${playercards[2].replace(/[0-9]/g,'')==='Scissors' ? Scissors : playercards[2].replace(/[0-9]/g,'')==='Paper' ? Paper : Rock})`: ""}} onMouseUp={cardSelectedfromDeck}></span>
                    </div>
                    <div className="compcard__holder">
                        <span style={{}} className="comp noshow"></span>
                        <span style={{}} className="comp noshow"></span>
                        <span style={{}} className="comp noshow"></span>
                    </div>

                    {/* backgroundImage: compcards[0] ? `url(${compcards[0].replace(/[0-9]/g,'')==='Scissors' ? Scissors : compcards[0].replace(/[0-9]/g,'')==='Paper' ? Paper : Rock})`: ""
                    backgroundImage: compcards[1] ? `url(${compcards[1].replace(/[0-9]/g,'')==='Scissors' ? Scissors : compcards[1].replace(/[0-9]/g,'')==='Paper' ? Paper : Rock})`: ""
                    backgroundImage: compcards[2] ? `url(${compcards[2].replace(/[0-9]/g,'')==='Scissors' ? Scissors : compcards[2].replace(/[0-9]/g,'')==='Paper' ? Paper : Rock})`: "" */}

                    {/* <h1>Pick {pick} cards</h1>
                    {
                        top.map((item, i) => {
                            return (
                                <span onMouseUp={cardHandlre} key={i} style={{ top: item, left: bottom[i], transform: `rotateZ(${rotate[i]}deg)` }}><p></p><p></p></span>)
                    })
                    } */}

                </div>
        }
        </div>
    )
}

export default Rockps
