import './App.css';
import Rockps from './Components/Rockps'
import Doublememory from './Components/Doublememory'
// import Fourofour from './Components/Fourofour'
import Navigator from './Components/Navigator'
import Home from './Components/Home'
import {HashRouter as Router, Switch, Route} from 'react-router-dom'

function App() {
  return (
    <Router>
      
      <div className="App"> 
      <Navigator />
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/rock-paper-scissors" component={Rockps} />
          <Route path="/double-memory" component={Doublememory} />
        </Switch>
      </div>
      
    </Router>
  );
}

export default App;
