import React from 'react'
import './Doublememory.css'

function Doublememory() {
    return (
        <div id='doublememory__container'>
            <span className="card">
                <span className="middlesquare">
                    <p className="almonds alms"></p>
                    <p className="liner line"></p>
                    <p className="almonds2 alms"></p>
                    <p className="liner2 line"></p>
                    <p className="almonds3 alms"></p>
                    <p className="liner3 line"></p>
                    <p className="almonds4 alms"></p>
                    <p className="liner4 line"></p>
                    <span className="eye"><p></p></span>
                    <p className="circlet"></p>
                </span>
            </span>
        </div>
    )
}

export default Doublememory
